.sentenceNative {
    display: flex;
    justify-content: center;
    width: 100%;
    margin: 0.5rem auto 2rem;

    align-items: center;
    color: #252528;
    font-size: 16px;
    font-weight: 500;
}

.BtnWrap {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    width: 100%;
    height: 7rem;

}

.btnWord {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    background-color: rgb(241, 246, 244);
    border-radius: 0.2rem;
    border: 0.1rem solid rgb(123, 116, 116);
    margin: 0 auto;

    font-weight: 700;
}

.answer {
    width: 100%;

    margin: 1rem;

    font-size: 1.4rem;
    font-weight: 700;
}

.result {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-self: center;

    font-size: 1.6rem;
    font-weight: 700;
}

.wrongWord {
    color: red;
}

.btnNext {
    border-radius: 10px;
    box-shadow: 0px 0px 2px 2px rgb(0, 0, 0);
    background-color: #0a0a23;
    padding: 0.2rem 1rem;
    margin: 1rem;

    color: #fff;
    font-size: 1.4rem;
}